import { Component, OnInit } from '@angular/core';
declare var $: any;
@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
  public ngOnInit() {
    $('.RegisterLink').click(function(){
      const url = 'https://maneshshaju.github.io/';
      window.open(url, '_self');
      });
      $('.EncounterLink').click(function(){
        const url = 'https://mohammed-rafeeque.github.io/';
        window.open(url, '_self');
        });
        $('.DwbLink').click(function(){
          const url = 'http://susmera-code.github.io/';
          window.open(url, '_self');
          });

    $(window).scroll(function () {
      if ($(window).scrollTop() >= 20) {
      // alert("hi");
      
      $('.header-nav').addClass('headerMini');
      $('.img1').addClass('imgToggle');
      // $('.mainnav').addClass('scrollNav');
     }
     else{
      $('.header-nav').removeClass('headerMini');
      $('.img1').removeClass('imgToggle');
     }
  });
}
}
